import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WorkFlowComponent } from './work-flow.component';
import { WorkflowAddEditComponent } from './workflow-add-edit.component';
import { RouterModule, Routes } from '@angular/router';


const routes: Routes = [
  { path: '', component: WorkFlowComponent },
  { path: 'add-flow', component: WorkflowAddEditComponent},
  {
    path: 'edit/:id', component: WorkflowAddEditComponent  },
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkflowRoutingModule { }
